<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>添加人员</p>
            </h3>
        </div>
        <div class="form_one_box">
            <yd-cell-group>
                <yd-cell-item>
                    <span slot="left">国家/地区</span>
                    <yd-input disabled slot="right" v-model="input1" ref="input10" placeholder="中国"></yd-input>
                </yd-cell-item>
                <yd-cell-item>
                    <span slot="left">&#12288;+86</span>
                    <yd-input slot="right" v-model="input2" placeholder="请输入手机号码"></yd-input>
                </yd-cell-item>
                <yd-cell-item>
                    <span slot="left">姓&#12288;名</span>
                    <yd-input slot="right" v-model="input3" placeholder="请输入姓名"></yd-input>
                </yd-cell-item>
                <yd-cell-item>
                    <span slot="left">密&#12288;码</span>
                    <yd-input type="password" slot="right" v-model="input4" placeholder="请输入密码"></yd-input>
                </yd-cell-item>
                <yd-cell-item>
                    <span slot="left">称&#12288;谓</span>
                    <yd-input slot="right" v-model="input5" placeholder="请输入称谓"></yd-input>
                </yd-cell-item>
            </yd-cell-group>
        </div>
        <div class="wallet_btn">
            <yd-button @click.native="save()" bgcolor="#5e1515" color="#fff" size="large" type="danger">保&#12288;存</yd-button>
        </div>
    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                input1: "中国",
                input2: "",
                input3: "",
                input4: "",
                input5: "",
                levels:"",
            }
        },
        methods: {
            goLast() {
                this.$router.go(-1);
            },
            linkTo(adr) {
                this.$router.push({
                    path: adr
                });
            },
            async save() {
                if (!this.input2 || this.input2.length !=11) {
                    this.$dialog.toast({
                        mes: "请输入正确的手机号",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                if (!this.input3) {
                    this.$dialog.toast({
                        mes: "请输入姓名",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                if (!this.input4 || this.input4.length < 6) {
                    this.$dialog.toast({
                        mes: "请输入大于6位的密码",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                if (!this.input5) {
                    this.$dialog.toast({
                        mes: "请输入称谓",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                let res = await ajax.Post(dataURL.main.createChild, {
                    phone: this.input2,
                    password: this.input4,
                    level: this.levels=='store'?"small_work":"partner",
                    name: this.input3,
                    title: this.input5,
                })
                this.$dialog.toast({
                    mes: "创建成功",
                    timeout: 1500,
                    icon: "success"
                });
                this.$router.go(-1);
            },
            async getUserinfo() {
                let res = await ajax.Get(dataURL.main.userInfo);
                console.log(res);
                this.levels = res.level;
            }
        },
        created() {
            this.getUserinfo();
        }
    }
</script>


